import { mapGetters } from "vuex";
import { mapState } from "vuex";
export default {
  data() {
    return {
      tokenSrc: ""
    };
  },
  created: function() {},
  mounted: function() {
    //falg
    isHomePage(0);
  },
  computed: {
    ...mapState({
      language: state => state.language.language
    })
  },
  methods: {
    showHeader() {
      let _this = this;
      // token
      var dataStr = JSON.stringify({ source: "YSG", otId: "1947364" });
      dataStr = "dataMap=" + dataStr;
      $.ajax({
        type: "post",
        url:
          "https://bird.ioliu.cn/v1/?url=http://www.xfplink.cn/issp/ApiService?method=xforceplus.sys.qToken",
        async: false,
        data: dataStr,
        dataType: "jsonp",
        success: function(json) {
          console.log("data>", json.token);
          _this.tokenSrc =
            "http://www.xfplink.cn/wx_invoiceTitle_confirm.html?u=" +
            json.token +
            "&m=YSG&b=X80";
          //
          if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
            openFile(_this.tokenSrc);
          } else {
            openUrl(_this.tokenSrc, "INVOICES");
          }
        },
        error: function() {}
      });
    },

    showManager() {
      let _this = this;
      // token
      var dataStr = JSON.stringify({ source: "YSG", otId: "1947364" });
      dataStr = "dataMap=" + dataStr;
      $.ajax({
        type: "post",
        url:
          "https://bird.ioliu.cn/v1/?url=http://www.xfplink.cn/issp/ApiService?method=xforceplus.sys.qToken",
        async: false,
        data: dataStr,
        dataType: "jsonp",
        success: function(json) {
          console.log("data>", json.token);
          _this.tokenSrc =
            "http://www.xfplink.cn/wx_invoiceCollector.html?u=" +
            json.token +
            "&m=YSG&b=X80";
          //
          if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
            openFile(_this.tokenSrc);
          } else {
            openUrl(_this.tokenSrc, "INVOICES");
          }
        },
        error: function() {}
      });
    },
    goBack() {
      this.$router.go(-1);
    }
  }
};



