import { mapGetters } from 'vuex'
import { mapState } from 'vuex'
export default {
    data() {
        return {
            dataList: [],
            nextPage: 1,
            noData: false,
            pageFlag:''
        }
    },
    created:function () {
        this.pageFlag = this.$route.query.pageFlag
    },
    methods: {
        getData:function () {
            let _this = this
            let params = {
                hotelid: localStorage.HOTELID,
                lang: localStorage.LANGUAGE,
                limit:6,
                page:_this.nextPage
            }
            this.$store.dispatch('getShoppingList', params).then(function (res) {
                var arrList = res.data.data.list
                _this.dataList = _this.dataList.concat(arrList)
                _this.nextPage = res.data.data.nextPage
            })
        },
        infinite:function (done) {
            let _this = this
            done(true);
            if (this.nextPage != '-1') {
                _this.getData()
            } else {
                done(false);
                _this.noData = true;
            }
        },
        goDetail:function (id) {
            let data = {};
            for(var key in this.dataList){
                if (this.dataList[key].id == id){
                    data = this.dataList[key];
                    break;
                }
            }
            this.$router.push({path:'/buy',query:{info:data}});
        }
    },
    mounted:function () {
        //falg
        isHomePage(0)
    },
    components: {
    },
computed: {
...mapState({
        language: state => state.language.language
    })
},

};



