
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import { mapState } from 'vuex'
import {mapGetters} from 'vuex'
import foot from '../foot.vue'
import menu from '../common/menu.vue'
import guestCenter from '../common/menu/guestCenter.vue'
import { getDistance,bubbleSort } from '../../config/mUtils'

export default {
data() {
return {
menuFlag:false,
centerFlag:false,
menuSlide:false,
centerSlide:false,
homeData:{},
startTime:'',
weather:{},
markFlag:false,
actionSheet:false,
lat:'',
lng:'',
idType:'',
hotelList:[],
distanceFlag:false,
myItems1: [
{
label: 'English',
method: () => {
this.hideAction('en')
this.langFlag = 'en'
}
},
{
label: '',
method: () => {
this.hideAction('zh')
this.langFlag = 'zh'
}
},
{
label: '',
method: () => {
this.hideAction('jp')
this.langFlag = 'jp'
}
},
{
label: 'Cancel',
method: () => {
_this.$store.dispatch('changeLanguage')
}
}
],
langFlag: 'en'

};
},
created:function () {
var _this = this
if(localStorage.lat == ''){
this.distanceFlag = false
} else {
this.distanceFlag = true
}

if(localStorage.LANGUAGE == 'en') {
this.langFlag = 'en'
} else if(localStorage.LANGUAGE =='zh') {
this.langFlag = 'zh'
}else{
this.langFlag = 'jp'
}
document.cookie ="test"

localStorage.menuFlag = 'unlogin'

localStorage.searchFlag = 'unlogin'

localStorage.footFlag = 1;

localStorage.stateFlag = 0;

var m=new Array("Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Spt","Oct","Nov","Dec");
var tDay = new Date();
this.startTime = m[tDay.getMonth()+1]+ ' ' + tDay.getDate()


this.$store.dispatch('firstLanguage');


let paramsById = {
groupid: localStorage.groupid
}
this.$store.dispatch('getGroupListById', paramsById).then((res) => {

this.hotelList = res.data.list;
for(var item in this.hotelList){
this.hotelList[item].distance=parseFloat(getDistance(localStorage.lng,localStorage.lat,res.data.list[item].lat,res.data.list[item].lng)).toFixed(1);
}

this.hotelList = bubbleSort(this.hotelList);


let params = {
hotelid: this.hotelList[0].hotelId,
lang: localStorage.LANGUAGE
}
this.$store.dispatch('getHome', params).then((res) => {
if(res.code == 0){

this.homeData = res.data

this.weather = res.data.wetherInfo;
}else{
this.$dialog.toast({mes: res.msg, timeout: 1000});
}

});

});
var _this = this;
$(document).ready(function () {
$('.mask-actionsheet').click(function () {
_this.$store.dispatch('changeLanguage')
})
$('.m-actionsheet a').click(function () {
_this.$store.dispatch('changeLanguage')
})

})
},
mounted: function () {
},
methods: {

showMenu:function () {

this.menuFlag = true; //
this.centerFlag = false; //
$('.n_index').css({
'position':'absolute',
'height': '100%',
'width': '100%',
'overflow': 'hidden'
})
if(this.menuSlide == false){
$('.n_index').css({
'left':'-5rem',
});
$('footer').css({
'left':'-5rem',
});

$('.mark').css({
'opacity':1,
},200);
this.menuSlide = true;
this.markFlag = true;
}
},

hideSlide:function () {
$('.n_index').css({
'position':'relative',
'height': 'auto',
'width': 'auto',
'overflow': 'auto'
})

if(this.menuSlide){
$('.n_index').css({
'left':0,
});
$('footer').css({
'left':0,
});
$('.mark').css({
'opacity':0,
});
this.menuSlide = false;
this.markFlag = false;
}
},
hideAction:function (key) {

localStorage.LANGUAGE = key;
var lanKey;
if(key){
lanKey = key;
}else{
lanKey = 'en';
}
this.$store.dispatch('updateLanguage', lanKey);
},
goHome:function (data) {
localStorage.HOTELID = data.hotelId
this.$router.push({path:'/home',query:{hotelid:data.hotelId, pageType:'unlogin', page:'unlogin'}})
},
openBooking:function () {
openBooking(localStorage.bookUrl)
}
},
components:{
'v-foot':foot,
'v-menu':menu,
'v-guestCenter':guestCenter,
Actionsheet,
Spinner
},
computed: {
...mapState({
language: state => state.language.language,
tabLanShow:state => state.language.tabLanShow
})
},
};



